*{
  background-color: #04379e;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #04379e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.h2_Text{
  color: orangered;
  text-align: center;
  font-size: 50px;
}

.h2Text{
  color: yellow;
  text-align: center;
  font-size: 30px;
}

.Paragraph{
  color: whitesmoke;
  font-size: 20px;
  text-align: center;
}

.btn{
  border-radius: 5px;
  padding: 10px;
  background-color: violet;
  border-color: whitesmoke;
  font-size: 20px;
  color: rgba(167, 5, 81, 0.852);
  box-shadow: 1cm;
  align-items: center;
  font-weight: bold;
  margin-left: 160px;
}

.hori{
  width: 1150px;
  font-size: 30px;
}